import request from "./request";
// get
export function getSafeList(params) {
  return request({
    method: "get",
    url: "/api-admin/v1/contract/audit",
    params,
  });
}
// update
export function addSafe(data) {
  return request({
    method: "post",
    url: "/api-admin//v1/contract/audit/add",
    data,
  });
}
// get chain config
export function getChainList() {
  return request({
    method: "get",
    url: "/api-admin/v1/swapconf/chains",
  });
}
