// 获取所有链
export function getChains() {
  return request({
    method: "get",
    url: "/api-admin/v1/chains",
  });
}

// 新增链
export function addChain(showName, chain) {
  return request({
    method: "post",
    url: "/api-admin/v1/chains/add",
    data: {
      show_name: showName,
      chain: chain,
    },
  });
}

// 修改链信息，目前只能修改链名称和链id:
export function updateChain(id, showName) {
  // 参数：（注意：只支持修改chain_name和chain_id,如未做修改，则保持原样返回给服务端）
  return request({
    method: "post",
    url: "/api-admin/v1/chains/update",
    data: {
      id: id,
      show_name: showName,
    },
  });
}

// 修改链状态
export function updateChainStatus(id, isDelete) {
  // 参数：（注意：只支持修改chain_name和chain_id,如未做修改，则保持原样返回给服务端）
  return request({
    method: "post",
    url: "/api-admin/v1/chains/status",
    data: {
      id: id,
      is_delete: isDelete,
    },
  });
}

// 调整链的显示顺序
export function changeIndexChain(chain1Id, chain2Id) {
  // 参数：（注意：只支持修改chain_name和chain_id,如未做修改，则保持原样返回给服务端）
  return request({
    method: "post",
    url: "/api-admin/v1/chains/changeIndex",
    data: {
      chain1_id: chain1Id,
      chain2_id: chain2Id,
    },
  });
}

