// getRobotList
export function getRobotList(params) {
  return request({
    method: "get",
    url: "/v1api/v3/stats/botStats",
    params: {
      pageNo: params.pageNo,
      pageSize: params.pageSize,
      sortField: params.sort,
      isAsc: params.direction === "desc" ? 0 : 1,
    },
  });
}
