import request from "./request"

// get ad list
export function getAdList(adv_type = 0) {
  return request({
    method: "get",
    url: "/api-admin/v1/advToken",
    params: {
      adv_type,
    },
  });
}

// add ad list
export function addAd(data, adv_type = 0) {
  return request({
    method: "post",
    url: "/api-admin/v1/advToken/add",
    data: {
      adv_type,
      ...data,
    },
  });
}

// update ad list
export function updateAd(data) {
  return request({
    method: "post",
    url: "/api-admin/v1/advToken/update",
    data,
  });
}
// delete ad
export function removeAd(id) {
  return request({
    method: "post",
    url: "/api-admin/v1/advToken/remove",
    data: {
      id,
    },
  });
}
