<template>
  <router-view />
  <van-tabbar
    class="tabs"
    :style="{ 'padding-bottom': safeBottomHeight }"
    route
    safe-area-inset-bottom
    :fixed="true"
    active-color="#fff"
    inactive-color="#A6A8B7"
    :z-index="4"
    :border="false"
  >
    <van-tabbar-item replace :to="{ name: 'Token' }"  icon-prefix="iconfont icon" icon="bizhongxinxi">
      币种信息
    </van-tabbar-item>

    <van-tabbar-item replace :to="{ name: 'Audit' }" icon-prefix="iconfont icon" icon="shenhe">
      收录审计
    </van-tabbar-item>
    <van-tabbar-item replace :to="{ name: 'Safe' }"  icon-prefix="iconfont icon" icon="shenji">安全审计</van-tabbar-item>
    <van-tabbar-item replace :to="{ name: 'HotAd' }" icon-prefix="iconfont icon" icon="resou">热搜广告</van-tabbar-item>
  </van-tabbar>
</template>
<script>
import store from '@/store'
let request = false
export default {
  name: 'Index',
  data() {
    return {}
  },
  beforeRouteEnter(to, from, next) {
    if (store.state.authToken) {
      if (from.name === 'Login' && store.state.userInfo.user_name) {
        next()
      } else {
        if (request) {
          next()
          return
        }
        request = true
        store
          .dispatch('getUserInfo')
          .then(res => {
            next()
          })
          .catch(err => {
            console.log(err)
            next()
          })
      }
    } else {
      next('/login')
    }
  },
  computed: {
    safeBottomHeight() {
      return (this.$store.state?.app?.safeBottomHeight || 0) / window.devicePixelRatio + 'px'
    }
  }
}
</script>

<style lang="scss" scoped>
.tabs {
  --van-tabbar-background: #2c3254;
  --van-tabbar-item-active-background: #2c3254;
  font-size: 12px;
  zoom: 0.83;
  .iconfont {
    font-size: 14px;
  }
}
</style>
