import { createRouter, createWebHistory } from 'vue-router'
import Index from '../views/index.vue'

const routes = [
  {
    path: '/',
    name: 'index',
    component: Index,
    children: [
      {
        path: '',
        name: 'Audit',
        component: () =>
          import(/* webpackChunkName: 'about' */ '../views/audit.vue')
      },
      {
        path: '/token',
        name: 'Token',
        component: () =>
          import(/* webpackChunkName: 'about' */ '../views/token.vue')
      },
      {
        path: '/safe',
        name: 'Safe',
        component: () =>
          import(/* webpackChunkName: 'about' */ '../views/safe.vue')
      },
      {
        path: '/hotAd',
        name: 'HotAd',
        component: () =>
          import(/* webpackChunkName: 'about' */ '../views/hotAd.vue')
      }
    ],
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/login')
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'not-found',
    redirect: '/'
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
