export function formatDate(val, dateType = 'YYYY-MM-DD HH:mm:ss') {
  let timeStamp = Number(val * 1000)
  if (!timeStamp) {
    timeStamp = val
  }
  let str
  const dateStr = new Date(timeStamp)
  str = dateType.replace('YYYY', dateStr.getFullYear())
  str = str.replace('MM', (dateStr.getMonth() + 1 < 10 ? '0' : '') + (dateStr.getMonth() + 1))
  str = str.replace('DD', (dateStr.getDate() < 10 ? '0' : '') + dateStr.getDate())
  str = str.replace('HH', (dateStr.getHours() < 10 ? '0' : '') + dateStr.getHours())
  str = str.replace('mm', (dateStr.getMinutes() < 10 ? '0' : '') + dateStr.getMinutes())
  str = str.replace('ss', (dateStr.getSeconds() < 10 ? '0' : '') + dateStr.getSeconds())

  return str
}

export function formatFixed(val, n = 8) {
  if (Number(val)) {
    return Number(val).toFixed(n)
  }
  return '0'
}

export function formatPercent(value, n = 2) {
  if (value > 0) {
    return Number((value * 100).toFixed(n)) + '%'
  }
  return '0%'
}

export function formatIcon(address, chain) {
  const pattern = /^(T|t)[0-9a-zA-Z]{33}-tron$|^[0-9a-zA-Z]{43,44}-solana$|^[0-9a-zA-Z]{20,50}-[0-z]{1,10}$|^0x[0-9a-zA-Z]{40}-[0-z]{1,10}$|^0x[0-9a-zA-Z::_]{1,}::[A-Z_]+-sui$/
  const pattern1 = /^(T|t)[0-9a-zA-Z]{33}$|^[0-9a-zA-Z]{43,44}$|^[0-9a-zA-Z]{20,50}$|^0x[0-9a-zA-Z]{40}$|^0x[0-9a-zA-Z::_]{1,}::[A-Z_]+$/
  if (!chain && pattern.test(address)) {
    const [a, c] = address.split('-')
    return `https://ave.s3.ap-east-1.amazonaws.com/token_icon/${c}/${a}.png`
  } else if (chain && address && pattern1.test(address)) {
    return `https://ave.s3.ap-east-1.amazonaws.com/token_icon/${chain}/${address}.png`
  }
  return ''
}

export function getPlatform(appType) {
  const o = {
    '-1': '全部',
    0: 'Android',
    2: 'ios'
  }
  return o?.[appType] || ''
}

export function obj2FormData(object) {
  const formData = new FormData()
  Object.keys(object).forEach((key) => formData.append(key, object[key]))
  return formData
}
