import request from "./request";
// get
export function getApplicationList(pageNo, pageSize, id) {
  return request({
    method: "get",
    url: "/api-admin/v1/token_request",
    params: {
      pageNo,
      pageSize,
      id,
    },
  });
}
// update
export function updateApplication(id, content) {
  return request({
    method: "post",
    url: "/api-admin//v1/token_request/update",
    data: {
      id,
      request_content: content,
    },
  });
}
// remove
export function removeApplication(id) {
  // post 请求
  return request({
    method: "post",
    url: "/api-admin/v1/token_request/delete",
    data: {
      id,
    },
  });
}
// remove
export function confirmApplication(id, remark) {
  // post 请求
  return request({
    method: "post",
    url: "/api-admin//v1/token_request/confirm",
    data: {
      id,
      admin_remark: remark,
    },
  });
}
// submit logo
export function submitLogo(data) {
  return request({
    method: "post",
    url: "/api-admin/v1/token_request/submit_logo",
    headers: { "Content-Type": "multipart/form-data" },
    data,
  });
}
export function getContractCheckResult(tokenId) {
  return request({
    method: "get",
    url: "/api-admin/v1/contract/contractImprove",
    params: {
      token_id: tokenId,
    },
  });
}
